.App{
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: rgb(223, 221, 221);
  background: linear-gradient(to right, #62d262, #f0fff3);
  color: #000;
}
.containerr{
width: 25rem;
display: grid;
font-size:1.3rem;
}
.box{
  height:19rem;
  width:24rem;
  /* background: linear-gradient(to right, #43cea2, #185a9d); */
  background-color: #fcfcfc;
  text-align: center;
  padding: 50px;
  color: #000;
  border-radius: 8px;

}
p{
  padding: 20px;
  background-color: #83a4d4;
}
.css{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: 1rem;
  background-color: #fff;

}
input{
  cursor: pointer;
}
@media only screen and (max-width:900px){
  .App{
    display: block;
    margin: auto;
    background: linear-gradient(to right, #83a4d4, #b6fbff);
    /* width: 100vw; */

  }
  .containerr{
    width: auto;
    padding: 2rem;
  }
  .box{
    /* margin: 40px; */
    width: 15rem;
height: 15rem;
padding: 10px;
margin: auto;
margin-top: 100px;
  }
  #copy{
display: none;
  }
  
  
}